<template>
    <div>
        <div class="header">
            <span v-if="open" @click="open = !open">&#x25B2; Collapse</span>
            <span v-if="!open" @click="open = !open">&#x25BC; Expand</span>
        </div>
        <slot v-if="open">
            <div>
                DefaultContent
            </div>
        </slot>
    </div>
</template>

<script>
export default {
  name: 'CollapsibleSection',
  data() {
    return {
      open: true,
    };
  },
};
</script>

<style scoped>
    .header {
        background: #e3e3e3;
        padding: 3px;
        cursor: pointer;
        border-radius: 4px;
    }
</style>
